import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import { Button, Menu, Dropdown, } from 'antd';

@inject(({globalStore})=>({
  operateMap: globalStore.operateMap,
}))
@observer
class View extends Component {

  handleClick = e => {
    const { onClick, itemdata } = this.props
    if(onClick) onClick(e.key, itemdata)
  }

  render() {
    const { itemdata, operateMap } = this.props
    let operList = operateMap[itemdata.status || 'default'] || []
    const menu = (
      <Menu onClick={this.handleClick} style={{minWidth: 80}}>
      {
        operList.map(item => (
          <Menu.Item key={item.key}>
            {item.label}
          </Menu.Item>
        ))
      }
      </Menu>
    );

    return (
      <Dropdown overlay={menu}>
        <Button type="link" className="ant-dropdown-link" onClick={e => e.preventDefault()}>
          操作 
        </Button>
      </Dropdown>
    )
  }
};
 
export default View;
 